.form {
  &__inner {
    padding-left: 15px;
    padding-right: 15px; }
  &__row {
    margin-left: -15px;
    margin-right: -15px;
    @include flex(flex-start, normal, row, wrap); }
  &__col {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    @include notlast {
      margin-bottom: 50px;
      @include xl {
        margin-bottom: 40px; }
      @include lg {
        margin-bottom: 30px; }
      @include md {
        margin-bottom: 20px; }
      @include sm {
        margin-bottom: 10px; } } }
  &__col-50 {
    width: 50%;
    @include xs {
      width: 100%; } }
  &__title {
    margin-bottom: 50px;
    @include xl {
      margin-bottom: 40px; }
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } } }

.input, .textarea {
  position: relative;
  input, textarea {
    height: 55px;
    width: 100%;
    background-color: $white;
    border: 0;
    border-bottom: 1px solid  $gray-lighten;
    padding: 18px 0;
    @include tr(border-color .3s);
    @include placeholder {
      text-transform: uppercase; }
    &:focus {
      border-color: $accent; }
    @include lg {
      height: 50px;
      padding: 15px 0; }
    @include md {
      height: 45px;
      padding: 12px 0; }
    @include sm {
      height: 40px; }
    @include xs {
      height: 30px;
      padding: 9px 0 5px; } } }

.textarea {
  textarea {
    overflow: hidden; } }
