.services {
  overflow: hidden;
  position: relative;
  &__top {
    position: relative;
    z-index: 1;
    @include flex(flex-start, center, row, wrap);
    .shadow-rect {
      margin-top: 30px;
      @include lg {
        margin-top: 15px; } }
    @include xl {
      display: block; } }
  &__title {
    max-width: 60%;
    margin-right: 45px;
    @include xl {
      margin-bottom: 15px;
      max-width: none;
      margin-right: 0; }
    @include lg {
      margin-bottom: 10px; } }
  &__blocks {
    position: relative;
    z-index: 1;
    padding-top: 50px;
    padding-bottom: 50px;
    @include xl {
      padding-top: 40px;
      padding-bottom: 40px; }
    @include lg {
      padding-top: 30px;
      padding-bottom: 30px; }
    @include md {
      padding-top: 20px;
      padding-bottom: 20px; } }
  &__block {
    position: relative;
    z-index: 1;
    max-width: 1240px;
    @include lg {
      margin-left: 0 !important; }
    &:nth-child(1) {}
    &:nth-child(2) {
      margin-left: 8%; }
    &:nth-child(3) {
      margin-left: 8%; }
    &:nth-child(4) {
      margin-left: auto; }
    &:nth-child(5) {}
    &:nth-child(6) {
      margin-left: 8%; }
    &:nth-child(7) {
      margin-left: 8%; }
    &:nth-child(8) {
      margin-left: auto; } } }

.service-block {
  $this: &;
  @include flex();
  margin-bottom: 50px;
  @include xl {
    margin-bottom: 40px; }
  @include lg {
    margin-bottom: 30px; }
  @include sm {
    margin-bottom: 20px; }
  @include xs {
    margin-bottom: 10px;
    display: block; }
  &__content {
    @include flex(center, normal, column);
    width: 50%;
    padding-right: 80px;
    padding-top: 15px;
    padding-bottom: 15px;
    @include xl {
      padding-right: 60px; }
    @include lg {
      padding-right: 40px; }
    @include md {
      padding-right: 30px; }
    @include sm {
      padding-right: 15px; }
    @include xs {
      width: auto;
      padding-left: 0 !important;
      padding-right: 0 !important; } }
  &__img {
    @include cover;
    // background-color: $gray-lighten
    padding-top: 30%;
    box-shadow: 0px 7px 19px 0px rgba(0, 0, 0, 0.17);
    // width: percentage(590/1240)
    width: 50%;
    @include xs {
      width: auto;
      padding-top: 50%; }
    @include xss {
      padding-top: 60%; } }
  &__title {
    margin-bottom: 50px;
    @include xl {
      margin-bottom: 40px; }
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } }
  &__text {
    margin-bottom: 40px;
    max-width: 570px;
    @include xl {
      margin-bottom: 30px; }
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 10px; } }

  &--img-left {
    #{$this}__content {
      padding-left: 80px;
      padding-right: 0 !important;
      @include xl {
        padding-left: 60px; }
      @include lg {
        padding-left: 40px; }
      @include md {
        padding-left: 30px; }
      @include sm {
        padding-left: 15px; } } }
  &--img-auto {
    #{$this}__img {
      background-size: contain;
      box-shadow: none; } }
  &--mb-0 {
    margin-bottom: 0 !important; } }
