body {
  background: $white;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  font-family: 'montserrat', Arial, sans-serif;
  font-weight: 300;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm; } }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  &.is-loaded:not(.no-animation) {
    animation: fadeIn 1s; } }

.container {
  max-width: 1480px;
  padding-left: 50px;
  padding-right: 50px;
  margin-left: auto;
  margin-right: auto;
  @include xl {
    padding-left: 30px;
    padding-right: 30px; }
  @include lg {
    padding-left: 15px;
    padding-right: 15px; }
  &--full {
    max-width: none;
    margin-left: 0;
    margin-right: 0; } }

.out {
  @include sm {
    padding-top: 105px; }
  @include xs {
    padding-top: 80px; } }

.logo {
  font-size: 0;
  display: inline-block; }

.logo-text {
  font-family: serif;
  span {
    font-family: 'montserrat', Arial, sans-serif;
    color: $accent; } }

.word {
  @include css-lock(60, 415, 320, 1399, font-size);
  line-height: .9;
  position: absolute;
  left: 0;
  top: 0;
  opacity: .1;
  z-index: 0; }

.shadow-rect {
  box-shadow: 0px 7px 19px 0px rgba(0, 0, 0, 0.17), inset 0px 0px 7.28px 0.72px rgba(0, 0, 0, 0.28);
  height: 16px;
  width: 100%;
  background-color: $white; }

.bg-lines {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
  left: 50%;
  transform: translate(-50%, 0);
  top: 0;
  max-width: 1450px; }

.bg-line {
  position: absolute;
  top: 0;
  bottom: 0;
  border-right: 1px solid  $gray-lighten;
  &:nth-child(1) {
    left: $line-left-1; }
  &:nth-child(2) {
    left: $line-left-2; }
  &:nth-child(3) {
    left: $line-left-3; }
  &:nth-child(4) {
    left: $line-left-4; } }

.link {
  text-transform: uppercase;
  font-weight: 500;
  color: $black;
  position: relative;
  @include tr(color .3s);
  @include hover {
    color: $accent; }
  @include after {
    width: 130px;
    border-top: 1px solid  $gray-lighten;
    display: inline-block;
    vertical-align: middle;
    position: relative;
    top: -.1em;
    margin-left: 15px; } }

.banner {
  @include cover;
  color: $white;
  padding: 70px 40px 60px;
  height: 100%;
  @include flex(space-between, normal, column);
  @include xl {
    padding: 60px 40px; }
  @include lg {
    padding: 50px 30px; }
  @include md {
    padding: 30px; }
  @include sm {
    padding: 20px; }
  &__phones {
    margin-bottom: 15px; }
  &__phone {
    @extend .title;
    @extend .title--h3;
    @extend .title--white;
    @extend .title--medium;
    margin-bottom: 5px; }
  &__mail {
    font-weight: 500; }
  &__title {
    max-width: 60%;
    margin-bottom: 65%;
    @include md {
      max-width: none; }
    @include sm {
      margin-bottom: 30%; } } }


