.breadcrumbs {
  font-size: 10px;
  color: $gray;
  text-transform: uppercase;
  li {
    display: inline-block; }
  a {
    text-decoration: underline;
    @include tr(color .3s);
    @include hover {
      color: $accent; } }
  .is-active {
    pointer-events: none;
    text-decoration: none; } }
